body {
  padding-top: 80px;
  background-color: rgb(207, 194, 220);
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  color: brown;
  box-shadow: 0 2px 6px 0 rgb(54, 4, 123);
  background-color: rgb(102, 9, 209);
  background-image: linear-gradient(
    to right,
    rgba(224, 191, 2, 0.4),
    rgba(0, 126, 175, 0.5)
  );
}

.nav-list {
  display: flex;
  list-style: none;
}

.nav-link {
  text-decoration: none;
  font-weight: bold;
  color: black;
  font-size: 20px;
}

.nav-list li:not(:last-child) {
  margin-right: 10px;
}

.nav-link-active {
  color: rgb(43, 255, 0);
}

.back-button {
  margin-bottom: 20px;
}

.movie-details {
  display: flex;
  margin-bottom: 30px;
}

.movie-poster {
  margin-right: 10px;
}

.additional-movie-info {
  border-top: 3px solid grey;
  border-bottom: 3px solid grey;
}

.movie-genre:not(:last-child) {
  margin-right: 10px;
}
